import React, { useState } from "react";
import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";
import profile from "../img/profile-image-placeholder.jpg";

function print() {
  window.print();
}

let cropper;

function Deal({ setInput, setName, input, onCrop }) {
  const [imageSrc, setImageSrc] = useState(profile);
  const [isCropperReady, setIsCropperReady] = useState(false);

  const onChange = (e) => {
    e.preventDefault();
    let files;
    if (e.dataTransfer) {
      files = e.dataTransfer.files;
    } else if (e.target) {
      files = e.target.files;
    }
    const reader = new FileReader();
    reader.onload = () => {
      setImageSrc(reader.result);
    };
    reader.readAsDataURL(files[0]);
  };

  // const cropperRef = useRef < HTMLImageElement > null;
  // const onCrop = () => {
  //   const imageElement = cropperRef?.current;
  //   const cropper = imageElement?.cropper;
  //   console.log(cropper.getCroppedCanvas().toDataURL());
  // };

  return (
    <div className="deal no-print">
      <p className="title"></p>
      <p>
        
      </p>
      <p></p>
      <div>
        <input
          type="text"
          maxLength="20"
          placeholder=""
          onChange={(e) => setInput(e.target.value)}
        />
        <label onClick={() => setName(input)}></label>
      </div>
      <Cropper
        className="cropper"
        // preview={require("./img/profile-image-placeholder.png")}
        src={imageSrc}
        style={{ height: 300, width: "90%" }}
        onInitialized={(instance) => {
          cropper = instance;
          setIsCropperReady(true);
        }}
        background={false}
        aspectRatio={1 / 1}
      />
      <div className="buttonBar">
        <label htmlFor="file1"></label>
        <input
          style={{ display: "none" }}
          id="file1"
          type="file"
          onChange={onChange}
          accept="image/*"
          disabled={!isCropperReady}
        />
        <label
          onClick={() => {
            if (isCropperReady) {
              onCrop(cropper.getCroppedCanvas().toDataURL());
              window.scrollTo(0, 0);
            }
          }}
        >
          
        </label>
        <label className="print" onClick={print}>
          
        </label>
      </div>
    </div>
  );
}

export default Deal;
