import React from "react";

const Card = ({ name, cropData }) => {
  return (
    <div className="card">
      <div className="card-body">
        <img src={cropData} alt="Profile" className="profile-img" />
        <p className="title"></p>
        <p className="name">{name}</p>

        <p className="description">
           689 
          
        </p>
      </div>

      <div className="card-footer">
        <p className="count"></p>
      </div>
    </div>
  );
};

export default Card;
